<template>
    <div
        class="section-home d-flex"
    >
        <v-container
            fluid
            class="d-flex justify-center px-16"
        >
            <transition name="fade" mode="out-in" class="d-flex">
                <v-row class="d-flex" key="b">
                    <v-hover v-slot="{ hover }">
                        <div
                            class="nav-btn d-flex d-md-none justify-center align-center rounded-tr-lg rounded-br-lg"
                            @click="dialog = !dialog"
                        >
                            <v-icon
                                :size="hover ? 30 : 25"
                                :color="hover ? 'black' : '#777777'"
                            >
                                mdi-view-grid
                            </v-icon>
                        </div>
                    </v-hover>
                    <v-dialog v-model="dialog" max-width="600">
                        <v-card class="px-6 py-4 rounded-lg">
                            <v-row>
                                <v-col
                                    cols="12"
                                    sm="4"
                                    md=""
                                    v-for="(r, i) in appData"
                                    :key="i"
                                >
                                    <div class="dialog-content-menu d-flex justify-center align-center rounded-lg"
                                        :style="{ backgroundColor: r.bgColor }"
                                        @click="changePage(r, 1)">
                                        <v-icon
                                            class="box-logo-icon"
                                            size="50"
                                            color="white"
                                        >
                                            {{ r.iconApp }}
                                        </v-icon>
                                        <span class="white--text">
                                            {{ r.name }}
                                        </span>
                                    </div>
                                </v-col>
                            </v-row>
                        </v-card>
                    </v-dialog>
                    <v-col cols="12">
                        <v-row>
                            <v-col cols="12" sm="12" md="2" lg="1" xl="2">
                                <v-row
                                    class="d-flex justify-center align-center"
                                >
                                    <v-col
                                        cols="6"
                                        sm="3"
                                        md="12"
                                        lg="12"
                                        xl="6"
                                        class="hidden-sm-and-down pt-0"
                                        v-for="(r, i) in appData"
                                        :key="i"
                                    >
                                        <div
                                            class="box-logo rounded-lg d-flex flex-column justify-center align-center"
                                            @click="changePage(r, 1)"
                                            :class="
                                                pageNow.name == r.name
                                                    ? 'actived'
                                                    : ''
                                            "
                                        >
                                            <v-icon
                                                class="box-logo-icon"
                                                size="50"
                                                :color="
                                                    pageNow.name == r.name
                                                        ? r.bgColor
                                                        : 'white'
                                                "
                                            >
                                                {{ r.iconApp }}
                                            </v-icon>
                                            <span
                                                class="box-logo-name caption mt-1"
                                                :style="
                                                    pageNow.name == r.name
                                                        ? { color: r.bgColor }
                                                        : { color: 'white' }
                                                "
                                            >
                                                {{ r.name }}
                                            </span>
                                        </div>
                                    </v-col>
                                </v-row>
                            </v-col>
                            <v-col cols="12" sm="12" md="10" lg="11" xl="10">
                                <v-row class="pl-2">
                                    <!-- box menu transition -->
                                    <transition-group name="list" tag="div" class="row pl-1">
                                    <v-col
                                        cols="12"
                                        sm="4"
                                        md="4"
                                        lg="3"
                                        xl="2"
                                        v-for="(r, i) in pageNow.menu"
                                        :key="'a'+i"
                                        class="box-menu-card pt-0 px-2 mb-1"
                                    >
                                        <v-hover v-slot="{ hover }">
                                        <div
                                            class="menu-card d-flex flex-column justify-center align-center rounded-lg"
                                            @click="toApp(r.link)"
                                            :style="hover ? 
                                                { backgroundColor: pageNow.color } : 
                                                { backgroundColor: 'white' }"
                                        >
                                            <div
                                                class="menu-card-box d-flex flex-column justify-center align-center"
                                            >
                                                <v-icon
                                                    class="menu-card-icon"
                                                    :size="hover ? '120' : '90'"
                                                    :color="hover ? 
                                                        'white' : 
                                                        pageNow.color
                                                    "
                                                >
                                                    {{ r.icon }}
                                                </v-icon>
                                                <span
                                                    class="menu-card-text text-center text-uppercase text-h6 font-weight-bold mt-4"
                                                    :style="{
                                                        color: 'white',
                                                    }"
                                                >
                                                    {{ r.title }}
                                                </span>
                                            </div>
                                        </div>
                                        </v-hover>
                                    </v-col>
                                    </transition-group>
                                </v-row>
                            </v-col>
                        </v-row>
                    </v-col>
                </v-row>
            </transition>
        </v-container>
    </div>
</template>

<script>
    import { mapGetters } from "vuex";
    export default {
        name: "Home",
        components: {
            //
        },

        mounted () {
            if (this.pageNow.name == '') {
                let firstData = this.appData[0]
                this.changePage(firstData, 1);
            }
        },

        computed: {
            ...mapGetters({
                appData: "appData",
            }),
        },

        data: () => ({
            dialog: false,
            pageNow: {
                name: "",
                color: "white",
                iconApp: "",
                menu: [],
                showPanel: true,
                showMenu: false,
            },
        }),

        methods: {
            toApp(q) {
                this.$router.push({ name: q });
            },
            changePage(q, s) {
                // console.log(q);
                this.dialog = false
                let delay = s == 0 ? 800 : 0;
                this.pageNow.showPanel = false;
                this.pageNow.color = q.bgColor;
                setTimeout(() => {
                    this.pageNow.name = q.name;
                    this.pageNow.menu = q.menu;
                    this.pageNow.iconApp = q.iconApp;
                    this.pageNow.showMenu = true;
                }, delay);
            },
            resetPage() {
                this.pageNow.showMenu = false;
                this.pageNow.color = "white";
                setTimeout(() => {
                    this.pageNow.name = "";
                    this.pageNow.menu = "";
                    this.pageNow.iconApp = "";
                    this.pageNow.showPanel = true;
                }, 800);
            },
        },
    };
</script>

<style scoped src="@/assets/home-page/home-page.css">
</style>