<template>
    <div id="chat-app" class="">
        <transition name="fade">
            <div class="loadingVue" v-if="getStatusLoadingChatApp == true">
                <scale-loader
                    :loading="getStatusLoadingChatApp"
                    :color="color"
                    :height="height"
                    :width="width"
                    class=""
                ></scale-loader>
            </div>
        </transition>
        <div class="" v-show="getStatusLoadingChatApp == false">
            <NavBtnCore />
            <NavBar />
            <Hero />
            <About />
            <Feature />
            <HowWork />
            <ScreenShot />
            <Team />
            <Pricing />
            <DividerComment />
            <FrequentlyQuestion />
            <Blog />
            <DividerDownload />
            <Contact />
            <DividerNewsletter />
            <Footer />
        </div>
    </div>
</template>

<script>
import ScaleLoader from "vue-spinner/src/ScaleLoader.vue";
import { mapGetters, mapActions } from "vuex";
export default {
    name: "ChatApp",
    components: {
        ScaleLoader,
        NavBtnCore: () =>
            import(
                /* webpackChunkName: "navbar" */ "@/components/CoreComponent/NavBtnCore.vue"
            ),
        NavBar: () =>
            import(
                /* webpackChunkName: "navbar" */ "@/components/ChatApp/NavBar.vue"
            ),
        Hero: () =>
            import(
                /* webpackChunkName: "hero" */ "@/components/ChatApp/Hero.vue"
            ),
        About: () =>
            import(
                /* webpackChunkName: "about" */ "@/components/ChatApp/About.vue"
            ),
        Feature: () =>
            import(
                /* webpackChunkName: "feature" */ "@/components/ChatApp/Feature.vue"
            ),
        HowWork: () =>
            import(
                /* webpackChunkName: "how-work" */ "@/components/ChatApp/HowWork.vue"
            ),
        ScreenShot: () =>
            import(
                /* webpackChunkName: "screen-shot" */ "@/components/ChatApp/ScreenShot.vue"
            ),
        Team: () =>
            import(
                /* webpackChunkName: "team" */ "@/components/ChatApp/Team.vue"
            ),
        Pricing: () =>
            import(
                /* webpackChunkName: "procing" */ "@/components/ChatApp/Pricing.vue"
            ),
        DividerComment: () =>
            import(
                /* webpackChunkName: "divider-comment" */ "@/components/ChatApp/DividerComment.vue"
            ),
        FrequentlyQuestion: () =>
            import(
                /* webpackChunkName: "frequently-question" */ "@/components/ChatApp/FrequentlyQuestion"
            ),
        Blog: () =>
            import(
                /* webpackChunkName: "blog" */ "@/components/ChatApp/Blog.vue"
            ),
        DividerDownload: () =>
            import(
                /* webpackChunkName: "divider-download" */ "@/components/ChatApp/DividerDownload.vue"
            ),
        Contact: () =>
            import(
                /* webpackChunkName: "contact" */ "@/components/ChatApp/Contact.vue"
            ),
        DividerNewsletter: () =>
            import(
                /* webpackChunkName: "divider-newsletter" */ "@/components/ChatApp/DividerNewsletter.vue"
            ),
        Footer: () =>
            import(
                /* webpackChunkName: "footer" */ "@/components/ChatApp/Footer.vue"
            ),
    },

    data: () => ({
        offsetTop: 0,
        color: "#6f51e6",
        height: "45px",
        width: "14px",
    }),

    computed: {
        ...mapGetters({
            getStatusLoadingChatApp: "getStatusLoadingChatApp",
        }),
    },

    created() {
        this.setStatusLoadingChatApp({
            status: false,
        });
    },

    beforeDestroy() {
        this.setStatusLoadingChatApp({
            status: true,
        });
    },

    methods: {
        ...mapActions({
            setStatusLoadingChatApp: "setStatusLoadingChatApp",
        }),
        onScroll(e) {
            console.log(e);
            this.offsetTop = e.target.scrollTop;
        },
    },
};
</script>

<style scoped>
#chat-app {
    overflow: hidden;
}
.loadingVue {
    position: absolute;
    z-index: 100;
    background:whitesmoke;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
}
.fade-enter-active,
.fade-leave-active {
    transition: opacity 1s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
}
</style>